import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { PigDetailComponent } from './detail/pig-detail.component';
import { PigListComponent } from './list/pig-list.component';
import { PigsComponent } from './pigs.component';

const routes: Routes = [
  {
    path: '',
    component: PigsComponent,
    children: [
      { path: 'list', component: PigListComponent },
      { path: 'detail/:id', component: PigDetailComponent },
      { path: '', pathMatch: 'full', redirectTo: '/list' },
    ],
  },
];

@NgModule({
  declarations: [PigDetailComponent, PigListComponent, PigsComponent],
  imports: [CommonModule, RouterModule.forChild(routes)],
})
export class PigsModule {}
